.news__main-sidebar{
    grid-area: sidebar;
    width: 100%;
    background: var(--very-dark-blue);
    padding: 2rem;
    gap: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.news__main-sidebar_header{
    color: var(--soft-orange);
}


.news__main-sidebar hr{
    border: 1px solid var(--dark-grayish-blue);
}