.news__main-article{
    display: grid;
    grid-template-columns: (2, 1fr);
    grid-template-areas: 'image content';
    grid-gap: 1rem;
    margin-bottom: 3rem;
}

.news__main-article_image{
    width: 110px;
    height: 150px;
}

.news__main-article img{
    width: 100%;
    height: 100%;
}

.news__main-article_content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
}

.news__main-article_content p:first-child{
    font-size: 2.5rem;
    color: var(--grayish-blue);
    font-weight: 700;
}

.news__main-article_content h2{
    color: var(--very-dark-blue);
    font-size: 700;
}

.news__main-article_content p:last-child{
    color: var(--dark-grayish-blue);
    line-height: 2rem;
    font-size: 15px;
}