.news__main-welcome{
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-area: welcome;
}

.news__main-welcome picture, .news__main-welcome source, .news__main-welcome img{
    width: 100%;
}

.news__main-welcome_content{
    margin-top: 1.5rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    /* flex-direction: row;
    justify-content: space-around;
    align-items: flex-start; */
    gap: 2rem;
}

.news__main-welcome_content-headline h1{
    font-weight: 800;
    font-size: 4rem;
    line-height: 4rem;
    color: var(--very-dark-blue);
}

.news__main-welcome_content-text{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    gap: 2rem;
}

.news__main-welcome_content-text p{
    font-size: 1rem;
    padding: 0rem 2rem 0rem 0rem;
    line-height: 1.5rem;
    color: var(--dark-grayish-blue);
}

.news__main-welcome_content-text button{
    width: 50%;
    height: 50px;
    color: var(--off-white);
    border: none;
    background: var(--soft-red);
    cursor: pointer;
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: .2rem;
}

.news__main-welcome_content-text button:hover{
    background: var(--very-dark-blue);
}

@media screen and (max-width: 500px){
    .news__main-welcome_content{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 1rem;
    }
    
    .news__main-welcome_content-headline h1 {
        font-size: 2.8rem;
        line-height: 3rem;
    }
    
    .news__main-welcome_content-text button {
        height: 50px;
    }
}