.news__main-container{
    max-width: 100%;
    width: 80%;
    margin-top: 5rem;
    height: 100%;
}

.news__nain-container_main{
    display: grid;
    grid-template-columns: 8fr 4fr;
    grid-template-areas: 'welcome sidebar';
    grid-gap: 2rem;
}

@media screen and (max-width: 500px){

    .news__main-container{
        width: 90%;
        height: 95%;
        margin-top: 0rem;
    }

    .news__nain-container_main{
        display: flex;
        flex-direction: column;
        gap: 1rem;
        width: 100%;
    }
}