* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body{
    background-color: var(--off-white);
    font-family: var(--font-family);
}

a{
    text-decoration: none;
}

ul{
    list-style-type: none;
}

.container__padding{
    margin-top: 2rem;
}

.App{ 
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}