.news__main-navbar{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
}

.news__main-navs ul{
    display: flex;
    flex-direction: row;
    gap: 2rem;
    justify-content: space-evenly;
    align-items: flex-end;
}

.news_main-menu{
    display: none;
    position: relative;
    margin-left: 1rem;
    /* background: blue; */
    /* height: 100vh; */
}


.news__main-navs ul li{
    padding: .2rem;
}

ul li a{
    color: var(--dark-grayish-blue);
}

ul li a:hover{
    color: var(--soft-red);
}

@media screen and (max-width: 500px){
    .news_main-menu {
        display: flex;
        margin-top: 0.5rem;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-end;
    }
    
    .news_main-menu img{
        width: 100%;
        z-index: 22;
    }

    .news__main-navs{
        display: none;
    }

    .news__main-menu_container {
        padding: 10rem 2rem;
        background: #fff;
        display: flex;
        position: absolute;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        margin-top: -2rem;
        /* gap: 2rem; */
        right: 0;
        margin-right: -1.4rem;
        height: 100vh;
        width: 262px;
        z-index: 1;
        box-shadow: 0 5px 8px 300px rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);
    }

    .news__main-menu_container ul li{
        margin-bottom: 2rem;
    }

    .news__main-menu_container ul li a{
        font-size: 1.2rem;
    }
}